// styling
import styles from './style.module.scss';

// components
import {NavLink} from 'react-router-dom';
import {Swiper, SwiperSlide} from 'swiper/react';
import {Pagination, EffectFade, Autoplay} from 'swiper';
import GradientBtn from '@ui/GradientBtn';
import Avatar from '@ui/Avatar';
import AnimatedText from 'react-animated-text-content';
import Spring from '@components/Spring';
//hooks
import { useState, useEffect } from 'react';
import axios from 'axios';
// assets
import video from '@assets/home/hero/particles.mp4';

// data placeholder
import hero from '@db/hero'

const Hero = () => {

    const [collectionData, setCollectionData] = useState([]);
    useEffect(() => {
       
        (async () => {
          
            const { data } = await axios.get(
              "https://api-goerli.reservoir.tools/collections/v5",
              {
                params: {
                  sortBy: "allTimeVolume",
                  limit: 6
                },
              }
            );

            setCollectionData(data.collections)
        })();
      }, []);
    return (
        <section className={styles.hero}>
            <video src={video}
                   className={styles.video}
                   autoPlay loop muted disablePictureInPicture playsInline controls={false} />
            <div className={`${styles.hero_container} container`}>
                <div className={`${styles.media} bg-secondary border-hover`}>
                    <Swiper className={styles.media_slider}
                            loop={true}
                            modules={[Pagination, EffectFade, Autoplay]}
                            effect="fade"
                            fadeEffect={{crossFade: true}}
                            slidesPerView="auto"
                            breakpoints={{
                                0: {
                                    slidesPerView: 1
                                }
                            }}
                            autoplay={{
                                disableOnInteraction: false,
                                pauseOnMouseEnter: false
                            }}
                            speed={1300}
                            pagination={{
                                clickable: true,
                                horizontalClass: styles.pagination
                            }}>
                        {
                            collectionData.map((item, index) => (
                                <SwiperSlide key={item.id}>
                                   <div className="d-flex flex-column g-30">
                                       <div>
                                        <NavLink className="h4 link-hover" to={`/author/${item.id}`} >
                                           
                                           <img className="border-10" src={item.image} alt={item.title} />
                                           </NavLink>
                                       </div>
                                       <div className="d-flex flex-column g-5">
                                           <NavLink className="h4 link-hover" to="/explore/item" >
                                               {item.title}
                                           </NavLink>
                                           <div className="d-flex align-items-center g-10">
                                               <Avatar src={item.image}
                                                       isVerified={item.isVerified}
                                                       alt={item.id}
                                                       size="xs"/>
                                               <NavLink className="text-sm text-light text-bold link-hover" to={`/author/${item.id}`}>
                                                   @{item.slug}
                                               </NavLink>
                                           </div>
                                       </div>
                                   </div>
                                </SwiperSlide>
                            ))
                        }
                    </Swiper>
                </div>
                <div className={styles.main}>
                    <AnimatedText
                        type="words"
                        animationType="throw"
                        duration={0.5}
                        tag="h1"
                        includeWhiteSpaces>
                        Discover the unique digital art of NFT
                    </AnimatedText>
                    <Spring delay={600}>
                        <p className={styles.main_text}>
                            Digital marketplace for crypto collectibles and non-fungible tokens. <br/>
                            Buy, sell, and discover exclusive digital assets.
                        </p>
                    </Spring>
                    <div className={styles.main_buttons}>
                        <Spring delay={800}>
                            <GradientBtn href="/ranking">Explore</GradientBtn>
                        </Spring>
                        {/* <Spring delay={1000}>
                            <NavLink className="btn btn--outline" to="/login">Create</NavLink>
                        </Spring> */}
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Hero