// utils
import {lazy} from 'react';
import { Suspense } from 'react';
import LoadingScreen from '@components/LoadingScreen';

// components
import Title from '@components/Title';
import Hero from '@layout/home/Hero';
const LiveAuctions = lazy(() => import('@layout/home2/LiveAuctions'));
const FeaturedCollections = lazy(() => import('@layout/home2/FeaturedCollections'));
const Features = lazy(() => import('@components/Features'));
const TopAuthors = lazy(() => import('@layout/home2/TopAuthors'));
const CTA = lazy(() => import('@components/CTA'));
const Brands = lazy(() => import('@layout/home2/Brands'));
const BestSellers = lazy(() => import('@layout/home/BestSellers'));


const Home2 = () => {
    return (
        <>
            <Title title="Home"/>
          
            <main>
            <Suspense fallback={<LoadingScreen visible/>}>
                <Hero/>
                {/* <LiveAuctions/> */}
                {/* <TopAuthors/> */}
                {/* <BestSellers/> */}

                <FeaturedCollections/>
                {/* <Features/> */}
                {/* <CTA/> */}
                {/* <Brands/> */}
                </Suspense>
            </main>
            
        </>
    )
}

export default Home2